import styles from "./App.module.css";
import multiple from "./img/multiple.webp";
import boolean from "./img/boolean.webp";
import text from "./img/text.svg";
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { toast } from 'react-toastify';

import React, { useState } from "react";
import QuestionInput from "./QuestionForm/QuestionInput";
import ExplainInput from "./QuestionForm/ExplainInput";
import OptionInputList from "./QuestionForm/OptionInputList";
import AnswerInput from "./QuestionForm/AnswerInput";
import QuestionPreviewCard from "./QuestionForm/QuestionPreviewCard";

function App() {
  const [title, setTitle] = useState("");
  const [questions, setQuestion] = useState([]);
  const [ques, setQues] = useState("");
  const [options, setOptions] = useState({});
  const [answer, setAnswer] = useState({});
  const [explanations, setExplanations] = useState({});
  const [questionType, setQuestionType] = useState("Part_1");
  const [explain, setExplain] = useState("");

  const handleAddQuestion = () => {
    if (ques.trim() === "") {
      toast.error("Cu hi khng c  trng", {
        pauseOnHover: false,
      });
      return;
    }
    
    if (questionType !== "Part_3" && Object.keys(options).length < 4 && Object.keys(explanations).length <4) {
      toast.error("Bn cn nhp cc la chn", {
        pauseOnHover: false,
      });
      return;
    }

    if (questionType === "Part_2" && Object.keys(explanations).length < 4) {
      toast.error("Bn cn nhp gii thch cho tng phn", {
        pauseOnHover: false,
      });
      return;
    }

    if (questionType === "Part_2" && Object.keys(answer).length < 4) {
      toast.error("Bn cn chn p n cho tng phn", {
        pauseOnHover: false,
      });
      return;
    }

    if (questionType === "Part_3" && Object.keys(answer).length ===0) {
      toast.error("Bn nhp chn p n ng", {
        pauseOnHover: false,
      });
      return;
    }

    if (questionType === "Part_1" && Object.keys(answer).length === 0) {
      toast.error("Bn cn chn p n ng", {
        pauseOnHover: false,
      });
      return;
    }
    
    if (questionType !== "Part_2" && explain.trim() === "") {
      toast.error("Bn cn nhp gii thch", {
        pauseOnHover: false,
      });
      return;
    }
    
    let newQuestion = {
      type: questionType,
      question: ques,
      explain: explain,
    };

    if (questionType === "Part_1" || questionType === "Part_2") {
      const labels = ["A", "B", "C", "D"];
      newQuestion.choices = labels.map((label) => ({
        label,
        text: options[label] || "",
        correct:
          questionType === "Part_1"
            ? answer.answer === label
            : answer[label] === "true",
        explain: questionType === "Part_2" ? explanations[label] || "" : "",
      }));
    }

    if (questionType === "Part_3") {
      newQuestion.answer = Object.values(answer).join("");
    }

    setQuestion([...questions, newQuestion]);
    setOptions({});
    setAnswer({});
    setExplanations({});
    setExplain("");
    setQues("");
    toast(" thm cu hi", {
      autoClose: 2000,
      className: styles.customToast,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: false,
      draggable: true,
    });
    
  };

  const handleSaveQuestion = () => {
    toast(" lu cu hi", {
      autoClose: 2000,
      className: styles.customToast,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: false,
      draggable: true,
    });
  };

  const handleOptionChange = (label, value) => {
    setOptions((prev) => ({ ...prev, [label]: value }));
  };

  const handleAnswer = (value, label = "answer") => {
    setAnswer((prev) => ({ ...prev, [label]: value }));
  };

  const handleExplainChange = (label, value) => {
    setExplanations((prev) => ({ ...prev, [label]: value }));
  };

  const handleDigitAnswer = (input) => {
    if (input.length > 4 || /^[A-Za-z]$/.test(input)) return;
    const answerArr = input.split("").slice(0, 4);
    const obj = {};
    answerArr.forEach((char, idx) => {
      obj[idx] = char;
    });
    setAnswer(obj);
  };

  const handleDelete = (index) => {
    const filtered = questions.filter((_, idx) => idx !== index);
    setQuestion(filtered);
  };

  const handleType = (value) => {
    setQuestionType(value);
  };

  return (
    <div className={styles.app}>
      <ToastContainer />
      <div className={styles.header}>
        <button onClick={handleAddQuestion} className={styles.add}>Thm cu hi</button>
        <button onClick={handleSaveQuestion} className={styles.save}>Lu</button>
      </div>
      <div className="container">
        <div className={styles.title}> c: <span>{questions.length} cu hi</span> </div>
        <div className={styles.wrapper}>
          <div className="row">
            <div className="col-md-1 d-flex justify-content-end">
              <div className={styles.titleCount}>1</div>
            </div>
            <div className="col-md-11 mb-3">
              <div>
                <div className={styles.titleName}>La chn loi </div>
                <div className={styles.titleDesc}>Chn loi  tng ng vi phn bn lm</div>
              </div>
              <div className={styles.partContent}>
                <div className={styles.choice}>
                  <div
                    className={
                      questionType === "Part_1"
                        ? `${styles.active} ${styles.choiceItem}`
                        : `${styles.choiceItem}`
                    }
                    onClick={() => handleType("Part_1")}
                  >
                    <img src={multiple} className={styles.choiceImg}></img>
                    <span className={styles.choiceName}>Chn p n</span>
                  </div>
                  <div
                    className={
                      questionType === "Part_2"
                        ? `${styles.active} ${styles.choiceItem}`
                        : `${styles.choiceItem}`
                    }
                    onClick={() => handleType("Part_2")}
                  >
                    <img src={boolean} className={styles.choiceImg}></img>
                    <span className={styles.choiceName}>ng sai</span>
                  </div>
                  <div
                    className={
                      questionType === "Part_3"
                        ? `${styles.active} ${styles.choiceItem}`
                        : `${styles.choiceItem}`
                    }
                    onClick={() => handleType("Part_3")}
                  >
                    <img src={text}></img>
                    <span className={styles.choiceName}>Nhp cu tr li</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="row">
            <div className="col-md-1 d-flex justify-content-end">
              <div className={styles.titleCount}>2</div>
            </div>
            <div className="col-md-11 mb-3">
              <div>
                <div className={styles.titleName}>Nhp cu hi</div>
                <div className={styles.titleDesc}>
                  Ch vit cu hi  y, khng vit cu tr li
                </div>
              </div>
              <div className={styles.partContent}>
                <div className={styles.questionInput}>
                  <QuestionInput ques={ques} onChange={setQues} />
                </div>
              </div>
            </div>
          </div>
          {(questionType === "Part_1" || questionType === "Part_2") && (
            <div className="row">
              <div className="col-md-1 d-flex justify-content-end">
                <div className={styles.titleCount}>3</div>
              </div>
              <div className="col-md-11 mb-3">
                <div>
                  <div className={styles.titleName}>Nhp la chn</div>
                  <div className={styles.titleDesc}>
                    Nhp cc la chn tng ng cho cu hi
                  </div>
                </div>
                <div className={styles.partContent}>
                  <OptionInputList
                    type={questionType}
                    options={options}
                    answer={answer}
                    onOptionChange={handleOptionChange}
                    onAnswerChange={handleAnswer}
                    explanations={explanations}
                    onExplainChange={handleExplainChange}
                  />
                </div>
              </div>
            </div>
          )}
          {questionType === "Part_3" && (
            <div className="row">
              <div className="col-md-1 d-flex justify-content-end">
                <div className={styles.titleCount}>3</div>
              </div>
              <div className="col-md-11 mb-3">
                <div>
                  <div className={styles.titleName}>Nhp p n</div>
                  <div className={styles.titleDesc}>
                    p n ch c 4 k t gm s 0-9 | du , | du -
                  </div>
                </div>
                <div className={styles.partContent}>
                  <AnswerInput
                    type={questionType}
                    answer={answer}
                    onDigitChange={handleDigitAnswer}
                  />
                </div>
              </div>
            </div>
          )}
          {(questionType === "Part_1" || questionType === "Part_3") && (
            <div className="row">
              <div className="col-md-1 d-flex justify-content-end">
                <div className={styles.titleCount}>4</div>
              </div>
              <div className="col-md-11 mb-3">
                <div>
                  <div className={styles.titleName}>Nhp gii thch</div>
                  <div className={styles.titleDesc}>
                    Vit gii thch cho p n trn
                  </div>
                </div>
                <div className={styles.partContent}>
                  <div className={styles.explain}>
                    <ExplainInput explain={explain} onChange={setExplain} />
                  </div>
                </div>
              </div>
            </div>
          )}
          <div className="row">
            <div className="col-md-1 d-flex justify-content-end">
              <div className={styles.titleCount}>4</div>
            </div>
            <div className="col-md-11 mb-3">
              <div>
                <div className={styles.titleName}>Xem li cu hi</div>
                <div className={styles.titleDesc}>
                  Xem li cu hi bn va mi nhp
                </div>
              </div>
              <div className={styles.partContent}>

                {questions.length > 0 && (
                  <QuestionPreviewCard
                    key={questions.length - 1}
                    index={questions.length - 1}
                    question={questions[questions.length - 1]}
                    onDelete={() => handleDelete(questions.length - 1)}
                  />
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
